/** @jsx jsx */
import React from 'react';
import {Global, css, jsx } from '@emotion/core';
import {Link} from 'react-router-dom';
import Typical from 'react-typical';
import { FaLinkedin, FaGithubSquare, FaFileDownload, FaArrowCircleLeft } from 'react-icons/fa';
import tictac from "../img/tictac.png";
import api from "../img/api.png";
import spotify from "../img/spotify.png"



 const Portfolio =() => {
const [array, setArray] = React.useState(["tictactoe", "api", "partypopper"]);
const [search, setsearch] = React.useState("");

    return (
 <div css={css`
    background-size:cover; height: auto; min-height: 100vh; font-family: 'Pattaya', sans-serif; margin-top: 50px; `}  >
        <h1 css={css`text-align:center; margin: 15px; padding: 20px;font-size: 2.5m; font-weight: bolder;  font-family: 'Lobster', cursive; margin-bottom: 15px;`}>Personal Projects</h1>
        
   <div  css={css`padding-top: 4vh;`}>
       
    <input type="text"  placeholder="Search"  css={css`width: 30vw; height: 5vh; transform: translate(35vw);
    border: none; border-bottom : 2.5px dotted black;:focus{outline: none;}; background: transparent;  font-size: 20px
    ::placeholder { 
        color: blue;

        opacity: 1; 
      }
      @media (max-width: 689px) { 
        width: 80vw; transform: translate(0vw); margin-left: 10vw  }
      `}
    onChange={(e) => setsearch(e.target.value.toLowerCase())} />
    </div>
    
      
<div css={css`display: flex; @media (max-width: 689px) { display: block;  }`}>
          
  {array[0].includes(search) && <div css={css`width:30vw; height: auto; margin-top:30px;@media (max-width: 689px) { width: 100%
`}>
        <Link to="/game">
            <img src={tictac} css={css`width: 33vw; height: 30vh;@media (max-width: 689px) { transform: scale(.9);
             margin-left: 0;display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;  }`} />
        </Link>  
        <h2 css={css`text-align: center;font-family: 'KoHo', sans-serif; font-weight: 1000;`}>Tictactoe</h2>
    </div>}

    {array[1].includes(search) && <div css={css`width:33vw; height: auto; margin-top:30px;`}>
        <Link to="/api">
            <img src={api} css={css` display: flex; justify-content: center; height: 30vh; margin-left:30%;@media (max-width: 689px) { margin-left: 0;display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;  }`} />
        </Link>  
        <h2 css={css`text-align: center;font-family: 'KoHo', sans-serif; font-weight: 1000;`}>Api</h2>
    </div>}
    {array[2].includes(search) &&  <div css={css`width:33vw; height: auto; margin-top:30px;`}>
        <a href="https://github.com/tawsif1/music_controller">
            <img src={spotify} css={css` display: flex; justify-content: center; height: 30vh; margin-left:15%;@media (max-width: 689px) { margin-left: 0;  }`} />
        </a>  
        <h2 css={css`text-align: center;font-family: 'KoHo', sans-serif; font-weight: 1000;`}>Party Popper</h2>
    </div>}
    

    
</div>


</div>
            
       
        
        
    );
}


export default Portfolio

