/** @jsx jsx */
import React, { Component } from 'react';
import { css, jsx } from '@emotion/core';
import portimg from "../img/portfolio.png";
import resimg from "../img/resume.png";
import others from "../img/others.png";
import Typical from 'react-typical';
import {Link} from 'react-router-dom';
import Video from "./bgvideo.mp4"



const Front = () => {


return(

    
<div css={css`
background:black;
height: 100vh;
width: 100vw;
position: fixed;
font-family: 'Pattaya', sans-serif;
@media (max-width: 689px) {height: 100%; }

`} >

<video className='videoTag' autoPlay muted  css={css`
width: 100vw;
height: 100vh;
position: absolute;

object-fit: cover;
z-index: -3;
@media (max-width: 689px) {object-fit: ; height: auto; z-index: 0; }
`}>
    <source src={Video} type='video/mp4' />
</video>

    <div css={css` display: flex; justify-content: center; transform: translateY(70vh); @media (max-width: 689px) {display: none }`}>
    
<button type= "button" css={css` background-color: transparent;
border: none; margin: 10px; padding: 5px; width: 10%; height: 10%;cursor: pointer; filter: brightnesss(120%);
`}>
    <Link to="/portfolio">
<img src={portimg} css={css` width: 50%; height: 50%;filter: brightness(150%) ;
:hover{
    filter: brightness(50%)
} `} /></Link>

< h4 css={css`font-family: 'KoHo', sans-serif; color: white;  font-size: 15px;`}>PORTFOLIO</h4>
</button>


<button type= "button" css={css` background-color: transparent;
border: none; margin: 10px; padding: 5px; width: 10%; height: 10%;cursor: pointer;
filter: brightnesss(120%);

`}>
    <Link to="/resume"><img src={resimg} css={css` width: 50%; height: 50%; filter: brightness(150%);
:hover{
    filter: brightness(50%)
}  `} />
</Link >

< h4  css={css`font-family: 'KoHo', sans-serif; color: white;  font-size: 15px;`}>RESUME</h4>

</button>



 


</div>
 <div css={css`display: block; background: black; transform: translate(0,30vh);height: 100%; @media (min-width: 689px) {display: none } `}>
 < Link to="/portfolio" style={{ textDecoration: 'none' }}>
    <div css={css`height: 25vh; display: flex; justify-content: center`}>
  <img src={portimg} css={css`  transform: scale(.35);filter: brightness(150%) ;
:hover{
    filter: brightness(50%)
} `} />
    </div>
    < h4 css={css`font-family: 'KoHo', sans-serif; color: white;  font-size: 15px; text-align: center;`}>PORTFOLIO</h4>
    </Link>
    
<Link to="/resume" style={{ textDecoration: 'none' }}>
 <div css={css`height: 25vh;  display: flex; justify-content: center; `}>

    <img src={resimg} css={css` transform: scale(.35); filter: brightness(150%);:hover{filter: brightness(50%)}  `} />
 </div>
    < h4  css={css`font-family: 'KoHo', sans-serif; color: white;  font-size: 15px; text-align: center`}>RESUME</h4>
 
   
</Link>
    
    
</div>





</div>

)





}

  
 

export default Front